import React, { useState } from "react";
import { Form, Button, Toast } from "react-bootstrap";
import { config } from "../configs/endpoints.js";
import { storage } from "../utils/session-storage.js";
import serialize  from "form-serialize";
import { useHistory } from "react-router-dom";
import axios from "axios";

function Login() {
  const [validated, setValidated] = useState(false);
  const history = useHistory();
  const [show, setShow] = useState(false);
  
  function getEndpoint() {
    return `${config.endpoints.basePath}${config.endpoints.login}`;
  }

  function handleSubmit(event) {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    } else {
      // let formData = ;
      console.log(
        "You clicked submit.", JSON.stringify(serialize(form, { hash: true })));
      event.preventDefault();

       axios({
  
        // Endpoint to send files
        url: "http://stronglasstough.com/velan/api.php?validateAdminLogin",
        method: "POST",
        headers: {
    
          // Add any auth token here
          // authorization: "your token comes here",
        },
    
        // Attaching the form data
        data: serialize(form, { hash: true }),
      })
    
        // Handle the response from backend here
        .then((res) => { 
          console.log(res);
            if(res.data?.user_info) {
              storage.set("user", "active");
              history.push("/pages/users");
            } else {
              alert("Invalid Credentials")
            }
        })
    
        // Catch errors if any
        .catch((err) => { });

    }

    setValidated(true);

  }

  // handleSubmit2 (event) {
  //   event.preventDefault();
  //   console.debug("data:", this)
  //   // let formData = this.props.auth.getIn(["emailSignIn", this.getEndpoint(), "form"]).toJS();
  //   // this.props.dispatch(emailSignIn(formData, this.getEndpoint()))
  //   //   .then(this.props.next)
  //   //   .catch(() => {});
  // }

  return (
    <div>
      <div className="d-flex align-items-center auth px-0">
        <div className="row w-100 mx-0">
          <div className="col-lg-4 mx-auto">
            <div className="auth-form-light text-left py-5 px-4 px-sm-5">
              <div className="brand-logo text-center">
                <img src={require("../../assets/images/logo.svg")} alt="logo" />
              </div>
              <h6 className="text-center font-weight-light">
                Sign in to continue.
              </h6>
              <Form
                className="pt-3 form"
                noValidate
                validated={validated}
                onSubmit={handleSubmit}
              >
                <Form.Group className="d-flex search-field">
                  <Form.Control
                    type="text"
                    name="user_name"
                    required
                    placeholder="Username"
                    size="lg"
                    className="h-auto"
                  />
                </Form.Group>
                <Form.Group className="d-flex search-field">
                  <Form.Control
                    type="password"
                    name="password"
                    required
                    placeholder="Password"
                    size="lg"
                    className="h-auto"
                  />
                </Form.Group>
                <div className="mt-3">
                  <Button
                    className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn"
                    type="submit" 
                    onClick={() => setShow(true)}
                  >
                    SIGN IN
                  </Button>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </div>

      <Toast bg={'danger'} key={'1'} onClose={() => setShow(false)} show={show} delay={3000} autohide>
        <Toast.Header>
          <img src="holder.js/20x20?text=%20" className="rounded me-2" alt="" />
          <strong className="me-auto">Error</strong>
          <small>11 mins ago</small>
        </Toast.Header>
        <Toast.Body className={'text-white'}>
          Enter Valid credentials.
        </Toast.Body>
      </Toast>
    </div>
  );
}

export default Login;
